.mapsearch {
  /* location */

  /* Auto Layout */
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 10px 20px 10px 15px;

  position: relative;

  /* Text_Grey */
  border: 1px solid #404040;
  box-sizing: border-box;
  border-radius: 5px;
}

.mapfilter {
  /* Auto Layout */
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 15px 15px;

  position: relative;
  width: 13vw;
  height: 5vh;

  /* Text_Grey */
  border: 1px solid #404040;
  box-sizing: border-box;
  border-radius: 5px;

  font-family: Avenir;
  font-style: normal;
  font-weight: normal;
  font-size: 1vw;
  line-height: 33px;
  /* identical to box height */
}

